import React from "react";
import ReactDOM from "react-dom";
import { Provider } from "react-redux";
import { BrowserRouter as Router, Route } from "react-router-dom";
import App from "./App";
import "./index.css";
import PrivateLayoutRoute from "./layouts/private/PrivateLayout";
import PublicLayoutRoute from "./layouts/public/PublicLayout";
import Cources from "./pages/cources/Cources";
import reportWebVitals from "./reportWebVitals";
import store from "./store/Store";

ReactDOM.render(
  <Provider store={store}>
    <Router>
      <Route path="/ap" component={App} />
      <PrivateLayoutRoute path="/cources" component={Cources} />
      <PublicLayoutRoute path="/" component={Cources} />
    </Router>
  </Provider>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
