import { Button } from "@material-ui/core";
import { canGetUserStream, getUserStream } from "@viero/common-web/media";
import { VieroLog } from "@viero/common/log";
import { createElement } from "@viero/ui/utils";
import { VieroWebRTCCommon } from "@viero/webrtc-common";
import { VieroWebRTCSFUClient } from "@viero/webrtc-sfu-client";
import { VieroWebRTCSignalingClient } from "@viero/webrtc-signaling-client";
import { useEffect, useRef, useState } from "react";
import "./style.css";
const channel = "sfds";

VieroLog.level = VieroLog.LEVEL.TRACE;

export default function Cources() {
  let me = useRef();
  const [state, setstate] = useState((window.vierochat = {}));
  let peers = useRef();
  let stream1 = useRef();
  useEffect(() => {
    me = document.querySelector("#me");
    peers = document.querySelector("#peers");

    const idBy = (socketId) => {
      return `p${socketId.replace("/", "---").replace("#", "___")}`;
    };

    state.signaling = new VieroWebRTCSignalingClient(
      "https://pathshala-e.herokuapp.com",
      channel
    );
    state.videochat = new VieroWebRTCSFUClient();
    state.videochat.addEventListener(
      VieroWebRTCCommon.EVENT.WEBRTC.STATE_DID_CHANGE,
      (evt) => {
        console.log(
          `WEBRTC(${evt.detail.direction}).STATE_DID_CHANGE`,
          evt.detail.state,
          ":",
          evt.detail.value,
          "in" === evt.detail.direction
            ? evt.detail.id
            : `SELF(${evt.detail.id})`
        );
      }
    );
    state.videochat.addEventListener(
      VieroWebRTCCommon.EVENT.PEER.DID_ENTER,
      (evt) => {
        console.log("PEER.DID_ENTER", evt.detail.peer.socketId);
        createElement("video", {
          attributes: {
            id: idBy(evt.detail.peer.socketId),
            playsinline: "",
            autoplay: "",
            class: "videoClass",
          },
          container: peers,
        });
      }
    );
    state.videochat.addEventListener(
      VieroWebRTCCommon.EVENT.PEER.DID_LEAVE,
      (evt) => {
        console.log("PEER.DID_LEAVE", evt.detail.peer.socketId);
        document.querySelector(`#${idBy(evt.detail.peer.socketId)}`).remove();
      }
    );

    state.videochat.addEventListener(
      VieroWebRTCCommon.EVENT.TRACK.DID_ADD,
      (evt) => {
        console.log("TRACK.DID_ADD", evt.detail.peer.socketId);
        document.querySelector(`#${idBy(evt.detail.peer.socketId)}`).srcObject =
          evt.detail.peer.stream;
      }
    );
    state.videochat.addEventListener(
      VieroWebRTCCommon.EVENT.TRACK.DID_REMOVE,
      (evt) => {
        console.log("TRACK.DID_REMOVE", evt.detail.peer.socketId);
        document.querySelector(`#${idBy(evt.detail.peer.socketId)}`).srcObject =
          evt.detail.peer.stream;
      }
    );

    state.videochat.addEventListener(VieroWebRTCCommon.EVENT.ERROR, (evt) => {
      console.error("ERROR", evt.detail.error, evt.detail.error.userData);
    });
  }, []);

  function endCall() {
    state.videochat.leave();
    peers.innerHTML = "";
    state.videochat.setStreams([]);
    me.innerHTML = "";
  }

  function startCall() {
    if (!canGetUserStream()) {
      return alert(
        "Your browser is missing the required technology for WebRTC!"
      );
    }
    state.videochat
      .join(state.signaling)
      .then(() => getUserStream({ video: true, audio: true }))
      .then((stream) => {
        stream1 = stream;
        state.videochat.setStreams([stream]);
        createElement("video", {
          attributes: { playsinline: "", autoplay: "" },
          properties: { srcObject: stream, muted: true },
          container: me,
        });
      });
  }

  function switchVideo() {
    stream1.getVideoTracks()[0].enabled = !stream1.getVideoTracks()[0].enabled;
  }

  function switchAudio() {
    stream1.getAudioTracks()[0].enabled = !stream1.getAudioTracks()[0].enabled;
  }
  return (
    <div>
      <div id="me" className="video-wrapper"></div>
      <div className="grid grid--5" id="peers" className="peers"></div>
      <div id="chat-buttons" className="actions">
        <Button
          variant="contained"
          color="primary"
          onClick={() => startCall()}
          className="actionBtn"
        >
          JOIN
        </Button>
        <Button
          variant="contained"
          color="secondary"
          onClick={() => endCall()}
          className="actionBtn"
        >
          End
        </Button>
        {stream1 && (
          <>
            <Button
              variant="contained"
              color="primary"
              onClick={() => switchVideo()}
              className="actionBtn"
            >
              Video
            </Button>
            <Button
              variant="contained"
              color="primary"
              onClick={() => switchAudio()}
              className="actionBtn"
            >
              Mic
            </Button>
            <Button
              variant="contained"
              color="primary"
              onClick={() => switchAudio()}
              className="actionBtn"
            >
              Screen Share
            </Button>
          </>
        )}
      </div>
    </div>
  );
}
